<style lang="less">
@import (less) '../../node_modules/v-color/dist/index.css';

@selector: .overvue;
@{selector} {
  &__settings {
    display: block;
  }

  &__badges {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    align-content: flex-start;
    position: relative;
    padding-top: var(--base);
    padding-bottom: calc(var(--font-bigger) + var(--base));

    &__add-button {
      color: var(--clr-red);
      font-size: var(--font-bigger);
      margin: var(--base-half);
    }
  }

  &__badge {
    &__wrap {
      display: block;
      position: relative;
    }

    &__edit-bar {
      white-space: nowrap;
      user-select: none;
      position: absolute;
      z-index: 1;
      left: var(--base-quarter);
      bottom: var(--base-quarter);
      visibility: hidden;

      @{selector}__badge__wrap:hover & {
        visibility: visible;
      }

      &--right {
        position: absolute;
        z-index: 1;
        top: 0;
        bottom: 0;
        right: var(--base-quarter);
        visibility: hidden;

        @{selector}__badge__wrap:hover & {
          visibility: visible;
        }
      }
    }

    &__sub-bar {
      position: absolute;
      top: 21px;
      left: -4px;
      padding: var(--base-quarter);
      display: flex;
      align-items: center;
      background: #fff;
      border: solid 1px #000;
      border-top: 0;
      justify-content: space-between;

      &__spacer {
        padding: 0 var(--base-quarter);
      }

      &__icon-style {
        width: 24px;
        text-align: center;
        margin-right: var(--base-quarter);
      }
    }

    .fas {
      &.active {
        color: var(--clr-red);
      }

      font-size: var(--font);
    }

    &__remove-btn {
      position: absolute;
      right: 0;
      top: var(--base-quarter);
    }

    &__duplicate-btn {
      position: absolute;
      right: 0;
      bottom: var(--base-quarter);
      color: var(--clr-red);
    }
  }

  .docsize {
    border: solid 1px var(--clr-grey);
    margin-top: var(--base);
    padding-top: 0;

    &--A2 {
      width: 42cm;
      height: 59.4cm;

      &--landscape {
        width: 59.4cm;
        height: 42cm;
      }
    }

    &--A3 {
      width: 29.7cm;
      height: 42cm;

      &--landscape {
        width: 42cm;
        height: 29.7cm;
      }
    }

    &--A4 {
      width: 21cm;
      height: 29.7cm;

      &--landscape {
        width: 29.7cm;
        height: 21cm;
      }
    }

    &--A5 {
      width: 14.8cm;
      height: 21cm;

      &--landscape {
        width: 21cm;
        height: 14.8cm;
      }
    }
  }
}
</style>

<template>
  <div class="overvue">
    <span class="overvue__settings">
      Docsize:
      <select v-model="docsize">
        <option>Bitte wählen</option>
        <option v-for="(label, index) in defaults.docsizes" :key="index">{{
          label
        }}</option>
      </select>
    </span>
    <span class="overvue__settings">
      Format:
      <select v-model="docsizeFormat">
        <option>Bitte wählen</option>
        <option
          v-for="(label, index) in defaults.docsizeFormats"
          :key="index"
          >{{ label }}</option
        >
      </select>
    </span>
    <span class="overvue__settings">
      Border: <input type="checkbox" v-model="showBorder" />
    </span>
    <div class="overvue__settings">
      Badges: <input type="checkbox" v-model="uniqueBadges" /> unique?
    </div>
    <div class="only-print">
      Be aware of 100% scaling.
    </div>
    <div
      v-bind:class="[
        'overvue__badges',
        `docsize docsize--${docsize}${
          docsizeFormat === 'landscape' ? '--' + docsizeFormat : ''
        }`
      ]"
    >
      <div class="overvue__badge" v-for="(data, key) in badges" :key="key">
        <div
          class="overvue__badge__wrap"
          v-on:mouseover="toggleEdit(key, true)"
          v-on:mouseleave="toggleEdit(key, false)"
        >
          <span class="overvue__badge__edit-bar--right">
            <i
              class="overvue__badge__duplicate-btn fas fa-plus-circle"
              v-on:click="duplicate(key)"
            ></i>
            <i
              class="overvue__badge__remove-btn fas fa-times-circle"
              v-on:click="remove(key)"
            ></i>
          </span>

          <span class="overvue__badge__edit-bar">
            <i
              class="overvue__badge__edit-btn fas fa-pen-square"
              v-bind:class="{
                active: data.isEditMode
              }"
            ></i>
            <span class="overvue__badge__sub-bar" v-if="data.isEditMode">
              <i
                title="badge size"
                class="fas"
                v-bind:class="{
                  'fa-search-minus': data.badge.size === 'big',
                  'fa-search-plus': data.badge.size === 'small'
                }"
                v-on:click="badgeStyle(key)"
              ></i>
              <ClrPikr
                title="choose badge background color"
                type="badge"
                v-bind:badgeKey="key"
                v-bind:show="data.showClrPicker"
                v-bind:clr="data.badge.clr"
                v-bind:onToggle="toggleClrPicker"
                v-bind:onChange="changeClr"
              />
              <span class="overvue__badge__sub-bar__spacer">
                |
              </span>
              <span>
                <i
                  title="upload icon"
                  class="overvue__badge__sub-bar__icon-upload fas fa-image"
                  v-on:click="triggerUpload(key)"
                ></i>
                <input
                  type="file"
                  accept="image/*"
                  capture
                  style="display:none"
                  ref="fileInput"
                  v-on:change="image($event, key)"
                />
                <i
                  title="icon style"
                  class="overvue__badge__sub-bar__icon-style fas"
                  v-bind:class="[
                    'active',
                    `fa-${faClassName(data.icon.style)}`
                  ]"
                  v-on:click="iconStyle(key)"
                ></i>
              </span>
              <ClrPikr
                title="choose icon background color"
                type="icon"
                v-bind:badgeKey="key"
                v-bind:show="data.showClrPicker"
                v-bind:clr="data.icon.clr"
                v-bind:onToggle="toggleClrPicker"
                v-bind:onChange="changeClr"
              />
              <span class="overvue__badge__sub-bar__spacer">
                |
              </span>
              <ClrPikr
                title="choose label color"
                type="label"
                v-bind:badgeKey="key"
                v-bind:show="data.showClrPicker"
                v-bind:clr="data.label.clr"
                v-bind:onToggle="toggleClrPicker"
                v-bind:onChange="changeClr"
              />
            </span>
          </span>

          <Badge
            v-bind:index="key"
            v-bind:showBorder="showBorder"
            v-bind:onChange="onChange"
            v-bind:data="data"
          ></Badge>
        </div>
      </div>
      <i
        class="overvue__badges__add-button fas fa-plus-circle"
        v-on:click="add()"
      ></i>
    </div>
  </div>
</template>

<script>
import ClrPikr from './ClrPikr.vue';
import Badge from './Badge.vue';

function copy(o) {
  // copy object or array
  let output, v, key;
  output = Array.isArray(o) ? [] : {};

  for (key in o) {
    v = o[key];
    if (v) {
      output[key] = typeof v === 'object' ? copy(v) : v;
    } else {
      output[key] = v;
    }
  }
  return output;
}

export default {
  name: 'Overvue',
  components: {
    Badge,
    ClrPikr
  },
  data() {
    return {
      defaults: {
        docsizes: ['A2', 'A3', 'A4', 'A5'],
        docsizeFormats: ['portrait', 'landscape'],
        styles: [
          { name: 'circle', fa: 'circle' },
          { name: 'triangle', fa: 'sort-up' },
          { name: 'round', fa: 'square' },
          { name: 'square', fa: 'square-full' },
          { name: 'star', fa: 'star' },
          { name: 'rhombus', fa: 'directions' }
        ]
      },
      docsize: 'A4',
      docsizeFormat: 'portrait',
      showBorder: true,
      uniqueBadges: false,
      badges: [
        {
          isEditMode: false,
          showClrPicker: '',
          badge: {
            size: 'big',
            clr: '#ffffff'
          },
          icon: {
            style: 'round',
            clr: '#cccccc',
            size: 'contain',
            x: 'center',
            y: 'center',
            base64:
              'data:image/png;base64,'
          },
          label: {
            value: 'Willy',
            clr: '#000000'
          }
        }
      ]
    };
  },
  methods: {
    onChange(type, data, index) {
      switch (type) {
        case 'label':
          this.badges[index].label.value = data;
          break;
        case 'isEditMode':
          this.badges[index].isEditMode = data;
          break;
      }
    },

    add() {
      this.badges.push({
        isEditMode: false,
        showClrPicker: '',
        badge: {
          size: 'big',
          clr: '#fff'
        },
        icon: {
          base64: '',
          style: 'round',
          clr: '#ccc'
        },
        label: {
          value: 'Neu',
          clr: '#000'
        }
      });
    },
    duplicate(lastKey) {
      // edit one change all
      let data = this.badges[lastKey];
      // unique badges
      if (this.uniqueBadges) {
        data = copy(this.badges[lastKey]);
      }
      data.isEditMode = false;
      this.badges.splice(lastKey + 1, 0, data);
    },
    remove(index) {
      this.badges.splice(index, 1);
    },

    badgeStyle(index) {
      const size = this.badges[index].badge.size;
      this.badges[index].badge.size = size === 'small' ? 'big' : 'small';
    },

    toggleEdit(index, isEditMode) {
      this.badges[index].isEditMode = isEditMode;

      if (!isEditMode) {
        this.badges[index].showClrPicker = '';
      }
    },

    triggerUpload() {
      this.$refs.fileInput[0].click();
    },
    image(event, index) {
      const reader = new FileReader();
      const files = event.target.files; // FileList object
      const file = files[0];

      reader.addEventListener(
        'load',
        () => {
          this.setImage(index, reader.result);
        },
        false
      );

      if (file) {
        reader.readAsDataURL(file);
      }
    },
    setImage(index, base64) {
      // 'data:' + file.type + ';base64,' +
      this.badges[index].icon.base64 = base64;
    },

    faClassName(iconStyle) {
      const foundStyle = this.defaults.styles.filter(style => {
        return style.name === iconStyle;
      })[0];
      return foundStyle.fa;
    },
    iconStyle(index) {
      const styles = this.defaults.styles;
      const actualStyle = this.badges[index].icon.style;
      let actualStyleIndex = 0;
      this.defaults.styles.map((style, index) => {
        if (style.name === actualStyle) {
          actualStyleIndex = index;
        }
      });
      const nextStyle = styles[actualStyleIndex + 1] || styles[0];
      // iterate through styles
      this.badges[index].icon.style = nextStyle.name;
    },

    toggleClrPicker(index, type) {
      const activeType = this.badges[index].showClrPicker;
      this.badges[index].showClrPicker = activeType !== type ? type : '';
    },
    changeClr(event, index, type) {
      this.badges[index][type].clr = event.hex;
    }
  }
};
</script>
